import { get } from "lodash/fp"
import React from "react"
import { v4 as uuid } from "uuid"
import { listManageForm } from "./list-manage-form"
import { BaseInterfaceComponent } from "../../../components/BaseInterfaceComponent/BaseInterfaceComponent"
import DisplayMode from "./components/DisplayMode"
import EditMode from "./components/EditMode"
import { ListInterfaceComponentProps } from "./types"
import { ComponentRendererModeContext } from "../../../contexts/ComponentRendererModeContext"
import { LayoutDefinition } from "../../../globalTypes"
import styled, { css } from "styled-components"

const Div = styled.div`
  ${({ styleString }: { styleString: string }) => css`
    ${styleString}
  `}
`

export class ListInterfaceComponent extends BaseInterfaceComponent<ListInterfaceComponentProps> {
  static defaultProps = {
    addItemLabel: "Add Item",
    allowDelete: true,
    allowReorder: true,
    orientation: "vertical",
    interleave: "none",
    unwrapped: false,
    userInterfaceData: {},
    valueKey: "data",
  }

  static getLayoutDefinition(): LayoutDefinition {
    return {
      category: "Display",
      name: "list",
      title: "List",
      icon: "unordered-list",
      componentDefinition: {
        classNames: ["display-mode"],
        component: "list",
        components: [],
      },
    }
  }

  static manageForm = listManageForm

  listId = uuid()

  render(): JSX.Element {
    const {
      addItemLabel,
      components,
      emptyText,
      interleave,
      orientation,
      preconfigured,
      unwrapped,
      userInterfaceData,
      getRootUserInterfaceData,
      setRootUserInterfaceData,
      valueKey,
    } = this.props

    // Get the list data from the data set
    const data = get(valueKey, userInterfaceData) || []

    return (
      <ComponentRendererModeContext.Consumer>
        {(mode) => {
          switch (mode) {
            case "display": {
              return (
                <Div styleString={this.props.style} className={"display-mode"}>
                  <DisplayMode
                    addItemLabel={addItemLabel}
                    components={components}
                    data={data}
                    getRootUserInterfaceData={getRootUserInterfaceData}
                    setRootUserInterfaceData={setRootUserInterfaceData}
                    getValue={this.getValue.bind(this)}
                    setValue={this.setValue.bind(this)}
                    description={emptyText}
                    interleave={interleave}
                    listId={this.listId}
                    orientation={orientation}
                    unwrapped={unwrapped}
                    userInterfaceData={userInterfaceData}
                    valueKey={valueKey}
                  />
                </Div>
              )
            }
            case "preview": {
              return (
                <Div styleString={this.props.style} className={"display-mode"}>
                  <DisplayMode
                    addItemLabel={addItemLabel}
                    components={components}
                    data={[]}
                    getRootUserInterfaceData={() => void 0}
                    setRootUserInterfaceData={() => void 0}
                    getValue={() => null}
                    setValue={() => null}
                    description={emptyText}
                    interleave={interleave}
                    listId={this.listId}
                    orientation={orientation}
                    unwrapped={unwrapped}
                    userInterfaceData={userInterfaceData}
                    valueKey={valueKey}
                  />
                </Div>
              )
            }
            case "edit": {
              // Repeat the component once per item in the list
              return (
                <EditMode
                  components={components}
                  data={data}
                  getRootUserInterfaceData={getRootUserInterfaceData}
                  setRootUserInterfaceData={setRootUserInterfaceData}
                  getValue={this.getValue.bind(this)}
                  setValue={this.setValue.bind(this)}
                  interleave={interleave}
                  preconfigured={preconfigured}
                  userInterfaceData={userInterfaceData}
                  valueKey={valueKey}
                />
              )
            }
          }
        }}
      </ComponentRendererModeContext.Consumer>
    )
  }
}
